export default {
	data() {
		return {
			modalName: null,
			textareaAValue: '',
			isTrue: ''
		}
	},
	onLoad() {

	},
	methods: {
		goSubmit(){
			var _this = this
			uni.navigateTo({
			    url: 'approvalMain?tabCur=0'
			});
		},
		textareaAInput(e) {
			this.textareaAValue = e.detail.value
		},
		radioChange(e) {
			this.isTrue = e.detail.value
		},
		goTimeLine() {
			uni.navigateTo({
			    url: 'timeline'
			});
		},
	}
}
